import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, 
  AngularFirestoreDocument  } from 'angularfire2/firestore'
import { Observable } from 'rxjs/Observable';
import { Technology } from '../Model/techology';

@Injectable()
export class FbTechnologyService {

  technologyCollection: AngularFirestoreCollection<Technology>;
  technologies: Observable<Technology[]>
  technologyDoc: AngularFirestoreDocument<Technology>;

  constructor(public afs: AngularFirestore) { 
    this.technologyCollection = this.afs.collection("Technology", ref => ref.orderBy('Name', 'asc'));
    this.technologies = this.technologyCollection.snapshotChanges().map(changes =>
    {
      return changes.map(a =>
      {
        const data = a.payload.doc.data() as Technology;
        data.Id = a.payload.doc.id;
        return data;
      });
    });
  }

  getTechnologies()
  {
    return this.technologies;
  }

  AddTechnology(technology: Technology)
  {
    this.technologyCollection.add(technology);
  }

  DeleteTechnology(technology: Technology)
  {
    this.technologyDoc = this.afs.doc(`Technology/${technology.Id}`);
    this.technologyDoc.delete();
  }

  UpdateTechnology(technology: Technology)
  {
    this.technologyDoc = this.afs.doc(`Technology/${technology.Id}`);
    this.technologyDoc.update(technology);
  }
}



