import { Component, OnInit } from '@angular/core';
import { Concept } from '../../Model/concept';
import { Question } from '../../Model/Question';
import { FbConceptService } from '../../services/fb-concept.service';
import { stagger } from '@angular/core/src/animation/dsl';
import { FbTechnologyService } from '../../services/fb-technology.service';
import { Technology } from '../../Model/techology';


@Component({
  selector: 'app-concept',
  templateUrl: './concept.component.html',
  styleUrls: ['./concept.component.css'],
  providers: [FbConceptService, FbTechnologyService]
})
export class ConceptComponent implements OnInit {

  newConcept: Concept = 
  {
    ConceptQuestion:
    {
      Question:"",
      Answer:""
    }
  };
  
  concepts: Concept[];
  technologies: Technology[];
  isEditConcept: boolean = false;
  editConcept: Concept = {};

  constructor(public fbConceptService: FbConceptService, 
    public fbTechnologyService:FbTechnologyService) { }

  ngOnInit() {
    //this.fbConceptService.GetConcepts().subscribe(concetList => this.concepts = concetList);
    this.fbTechnologyService.getTechnologies().subscribe(techs => this.technologies = techs);
    this.fbConceptService.GetConcepts().subscribe(conceptList => this.concepts = conceptList);
  }

  editConceptItem(concept: Concept)
  {
    this.isEditConcept = true;
    this.editConcept = concept;
  }
  
  updateConceptItem(concept: Concept)
  {
    this.isEditConcept = false;
    this.fbConceptService.UpdateConcept(concept);
  }

  cancelConceptItem(concept: Concept)
  {
    this.isEditConcept = false;
  }

  techChange()
  {
    this.concepts = null;
    console.log(this.newConcept.TechnologyId);
    let list = this.fbConceptService.GetConcepts().subscribe(conceptList => {
      return conceptList.filter(
        c =>  (c.TechnologyId = 'qIbhJa7U4V4pkxMxywKj'))
    });
    console.log(list);
    //.subscribe(concetList => this.concepts = concetList.filter(c => c.TechnologyId == this.newConcept.TechnologyId));
  }
}



