import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router'
import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { environment } from '../environments/environment';
import { AngularFireAuth } from 'angularfire2/auth';

import { AppComponent } from './app.component';
import { MenuComponent } from './menu/menu.component';
import { ContentComponent } from './content/content.component';
import { FooterComponent } from './footer/footer.component';
import { TechnologyComponent } from './content/technology/technology.component';
import { ConceptComponent } from './content/concept/concept.component';
import { PageNotFoundComponent } from './not-found.component';
import { Technology_Routes } from './app.route';
import { FbTechnologyService } from './services/fb-technology.service';
import { AddTechnologyComponent } from './content/technology/add-technology/add-technology.component';
import { LearningComponent } from './content/learning/learning.component';
import { AddConceptComponent } from './content/concept/add-concept/add-concept.component';
import { AuthService } from './services/auth.service';
import { HomeComponent } from './content/home/home.component';


@NgModule({
  declarations: [
    AppComponent,
    MenuComponent,
    ContentComponent,
    FooterComponent,
    TechnologyComponent,
    ConceptComponent,
    PageNotFoundComponent,
    AddTechnologyComponent,
    LearningComponent,
    AddConceptComponent,
    HomeComponent
  ],
  imports: [
    BrowserModule,
    AngularFireModule.initializeApp(environment.firebase, 'angularfs'),
    AngularFirestoreModule,
    RouterModule.forRoot(Technology_Routes),
    FormsModule,
    HttpClientModule
  ],
  providers: [AngularFireAuth],
  bootstrap: [AppComponent]
})
export class AppModule { }



