import { Component, OnInit } from '@angular/core';
import { Technology } from '../../../Model/techology';
import { FbTechnologyService } from '../../../services/fb-technology.service';


@Component({
  selector: 'app-add-technology',
  templateUrl: './add-technology.component.html',
  styleUrls: ['./add-technology.component.css']
})
export class AddTechnologyComponent implements OnInit {

  tech: Technology =
  {
    Name: "",
    Description:""
  }

  isAddNewEnabled = false;

  constructor(private fbTechnologyService: FbTechnologyService) { }

  ngOnInit() {
  }

  onSubmit()
  {
    if(this.tech.Name != '' && this.tech.Description != '')
    {
      this.fbTechnologyService.AddTechnology(this.tech);
      this.tech.Name = this.tech.Description = "";
      this.isAddNewEnabled = false;
    }
    
  }
}



